import Link from "next/link";
import { RouteNames } from "@/lib/routesInfo";
import { LongPhotoCardProps } from "./LongPhotoCard.interface";
import DefaultLatestNewsImage from '@/public/images/Default-Long-Card-Image.png'
import Image from "next/image"
import GradientHover from "../GradientHover/GradientHover";

export default function LongPhotoCard(props: LongPhotoCardProps) {

    //Компонент изображения (Показывает либо переданное изображение, либо стандартное)
    const ImageComponent = () => {
        return (
            <Image
                alt={props.alt ? props.alt : "Зенит-Ижевск стандартная обложка свежей нвости"}
                src={props.src ? props.src : DefaultLatestNewsImage}
                className="w-full h-full object-cover scale-110 hover:scale-100 transition-all duration-300"
            />
        )
    };

    //Блок с переданным контентом
    const childrenBlock =
        <div className="absolute h-full z-21 pointer-events-none inset-0">
            {props.children}
        </div>

    //Выбираем либо обычный блок, либо блок-ссылку
    if (props.href) {
        return (
            <Link
                className="w-[360px] h-[480px] rounded-[30px] bg-transparent overflow-hidden relative group"
                style={{
                    WebkitMaskImage: '-webkit-radial-gradient(white, black)'
                }}
                href={props.href}
            >
                <ImageComponent />
                <GradientHover />
                {childrenBlock}
            </Link>
        )
    } else {

        return (
            <div className="w-[360px] h-[480px] rounded-[30px] bg-transparent overflow-hidden relative group"
                style={{
                    WebkitMaskImage: '-webkit-radial-gradient(white, black)'
                }}
            >
                <ImageComponent />
                <GradientHover />
                {childrenBlock}
            </div>
        )
    }
}