import { TeamElementTable } from "./TeamElementTable.type";
import { TournamentTableElementProps } from "./TournamentTableElement.interface";

export default function TournamentTableElement(props: TournamentTableElementProps) {

    const borderWidthValue = 2; //px
    const borderRadiusValue = 30; //px

    const colSpan = [
        'col-span-4',
        'col-span-20',
        'col-span-5',
        'col-span-5',
        'col-span-5',
        'col-span-5',
        'col-span-10',
        'col-span-4'
    ];

    const tableHeadNames = [
        { name: '', },
        { name: props.groupName, },
        { name: 'И', },
        { name: 'В', },
        { name: 'Н', },
        { name: 'П', },
        { name: 'М', },
        { name: 'О', }
    ];

    const teamInfo = props.teams.reduce<[number, string, number, number, number, number, string, number][]>((accumulator, team, index) => {
        accumulator.push([index, team.teamName, team.games, team.wins, team.draw, team.defeat, `${team.scoredGoals} - ${team.missedGoals}`, team.score]);
        return accumulator;
    }, []);

    const TableElement = (
        { className = '', title = '' }:
            { className?: string | null | undefined, title?: string | null | undefined }
    ) => {
        return (
            <p
                className={`text-center bg-[#00102D] ${className}`}
            >
                {title ? title : ''}
            </p>
        )
    }

    return (
        <div className={`w-full bg-white overflow-hidden border-[${borderWidthValue}px] gap-[${borderWidthValue}px] rounded-[${borderRadiusValue}px] grid grid-cols-58`}>
            {/* Шапка таблицы (названия колонок) */}
            {tableHeadNames.map((th, index) => (
                <TableElement key={th.name} title={th.name} className={colSpan[index]} />
            ))}
            {}
        </div>
    )
}

